import React, { useState, useEffect, useMemo } from 'react';
import { Form } from '@rocketseat/unform';
import * as Yup from 'yup';

import sockeio from 'socket.io-client';
import { MdNotifications } from 'react-icons/md';
import { parseISO, formatRelative } from 'date-fns';
import pt from 'date-fns/locale/pt-BR';

import Label from '../../components/Label';
import Button from '../../components/Button';

import Modal from '../../components/Modal';

import { Question, InputTextArea, Small } from './styles';

import {
  Container,
  Badge,
  NotificationList,
  Scroll,
  Notification,
} from './styles';
import api from '../../services/api';

import { toast } from 'react-toastify';

const schema = Yup.object().shape({
  answer: Yup.string().required('A resposta é obrigatória'),
});

export default function Notifications() {
  const [visible, setVisible] = useState(false);
  const [helpOrders, setHelpOrders] = useState([]);
  const [helpOrder, setHelpOder] = useState();

  const [showModal, setShowModal] = useState(false);

  const hasUnAnswered = useMemo(
    () => !!helpOrders.find(help => help.answer_at === null),
    [helpOrders]
  );

  async function loadNotifications() {
    const res = await api.get('notifications');

    const data = res.data.map(notification => ({
      ...notification,
      createdAtFormatted: formatRelative(
        parseISO(notification.createdAt),
        new Date(),
        {
          locale: pt,
        }
      ),
    }));

    setHelpOrders(data);
  }

  useEffect(() => {
    loadNotifications();
  }, []);

  const socket = useMemo(
    () =>
      sockeio(process.env.REACT_APP_BASE_URL, {
        query: {},
      }),
    []
  );

  useEffect(() => {
    socket.on('HELP_ORDER_CREATE_NOTIFICATION', helpOrder => {
      const data = {
        ...helpOrder,
        createdAtFormatted: formatRelative(
          parseISO(helpOrder.createdAt),
          new Date(),
          {
            locale: pt,
          }
        ),
      };
      setHelpOrders([...helpOrders, data]);
      loadNotifications();
    });
  }, [socket, helpOrders]);

  function handleToggleVisible() {
    setVisible(!visible);
  }

  async function handleSubmitAwswer(data, { resetForm }) {
    try {
      await api.post(`help-orders/${helpOrder.id}/answer`, {
        answer: data.answer,
      });
      toast.success('Resposta enviada com sucesso');
      setShowModal(!showModal);
      resetForm();
    } catch (err) {
      toast.error('Ops, Erro ao responder');
    }
  }

  function showModalAnswer(helpOrder) {
    console.log(helpOrder)
    if (helpOrder) {
      setHelpOder(helpOrder);
      setShowModal(!showModal);
    }
  }

  return (
    <div>
      <Container>
        <Badge
          onClick={handleToggleVisible}
          hasUnAnswered={hasUnAnswered}
          total={helpOrders.length}
        >
          <MdNotifications color="#656565" size={24} />
        </Badge>

        <NotificationList visible={visible}>


        {helpOrders.length <= 0 &&
          <div style={{textAlign: "center"}}>
            <small style={{color: "#fff"}}>Nenhuma pergunta para responder!</small>
          </div>
        }

          {helpOrders.length > 0 &&
            <Scroll>
              {
                helpOrders.map(help => (
                  <Notification key={String(help.id)} onClick={() => showModalAnswer(help)}>
                    <p>{help.question}</p>
                    <time>
                      {help.createdAtFormatted} - {help.student.name}
                    </time>
                  </Notification>
                ))
              }
            </Scroll>
          }
        </NotificationList>
      </Container>

      {helpOrder &&
        <Modal
          title={'RESPONDER DÚVIDA'}
          isShow={showModal}
          onClose={() => setShowModal(!showModal)}
          width="450px"
        >

          <Question>{helpOrder.question}</Question>

          <Small>{helpOrder.createdAtFormatted} - {helpOrder.student.name}</Small>

          <Form id="answer" schema={schema} onSubmit={handleSubmitAwswer}>
            <Label>SUA RESPOSTA</Label>
            <InputTextArea multiline name="answer" />
            <Button type="submit" label="Enviar resposta"></Button>
          </Form>
        </Modal>
      }
    </div>
  );
}
